@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100;200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html {
	font-size: 62.5%;
}

body {
	font-family: "Kanit", sans-serif;
	padding: 2rem 6rem;

}

@media screen and (max-width: 800px) {
	body{
		padding: 1rem;
	}
}
a {
	text-decoration: none;
	color: black;
}

.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0;
}/*# sourceMappingURL=homepage.styles.css.map */
.menu {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}/*# sourceMappingURL=directory.styles.css.map */
.menu-item {
  min-width: 30%;
  height: 24rem;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  overflow: hidden;
}
@media screen and (max-width: 800px) {
  .menu-item {
    height: 20rem;
  }
}
.menu-item:first-child {
  margin-right: 7.5px;
}
.menu-item:last-child {
  margin-left: 7.5px;
}
.menu-item.large {
  height: 38rem;
}
@media screen and (max-width: 800px) {
  .menu-item.large {
    height: 20rem;
  }
}
.menu-item:hover {
  cursor: pointer;
}
.menu-item:hover .menu-content {
  background-color: #bce7fd;
}
.menu-item:hover .background-image {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.menu-content {
  position: absolute;
  height: 9rem;
  padding: 0 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  border-radius: 5px;
  background-color: rgba(188, 231, 253, 0.4);
  transition: all 0.5s ease-in;
}
.menu-content .title {
  font-weight: bold;
  margin-bottom: 6px;
  font-size: 2.2rem;
  color: #4a4a4a;
}
.menu-content .subtitle {
  font-weight: lighter;
  font-size: 1.6rem;
}

.background-image {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  transition: all 6s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}/*# sourceMappingURL=menu-items.styles.css.map */
.cartIcon {
  height: 2.5rem;
  width: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.cartIcon span {
  position: absolute;
  color: black;
  border-radius: 50%;
  font-weight: bold;
}/*# sourceMappingURL=cart-icon.styles.css.map */
.custom-button {
  min-width: 1.5rem;
  text-transform: uppercase;
  background-color: black;
  color: white;
  font-size: 1.5rem;
  letter-spacing: 0.5px;
  padding: 1.5rem 3rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  transition: 0.3s all ease-in;
}
.custom-button:hover {
  background-color: white;
  color: black;
}
.custom-button:focus {
  outline: 0;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
@media screen and (max-width: 800px) {
  .custom-button {
    width: 100%;
  }
}

.inverted {
  background-color: white;
  color: black;
  border: 1px solid black;
}
.inverted:hover {
  background-color: black;
  color: white;
}/*# sourceMappingURL=custom-button.styles.css.map */
.cart-dropdown {
  position: absolute;
  top: 9rem;
  right: 6rem;
  width: 25rem;
  height: 35rem;
  padding: 2rem;
  background-color: white;
  z-index: 5;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
}
@media screen and (max-width: 800px) {
  .cart-dropdown {
    right: 1rem;
  }
}
.cart-dropdown .empty-message {
  font-weight: lighter;
  display: block;
  align-self: center;
  margin: auto 0;
  font-size: 1.8rem;
}
.cart-dropdown .cart-items {
  display: flex;
  flex-direction: column;
  height: 25rem;
  overflow-y: scroll;
}
.cart-dropdown .cart-items::-webkit-scrollbar {
  width: 1px;
}
.cart-dropdown button {
  margin-top: auto;
}
.cart-dropdown button svg {
  height: 1.5rem;
  width: 1.5rem;
  fill: white;
  margin-left: 1rem;
}
.cart-dropdown button:hover svg {
  fill: black;
}/*# sourceMappingURL=cart-dropdown.styles.css.map */
.cart-item {
  width: 100%;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart-item img {
  width: 30%;
  height: 100%;
}
.cart-item .item-details {
  font-size: 1.4rem;
  height: 100%;
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}/*# sourceMappingURL=cart-items.styles.css.map */
